* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px
}

input {
  color: gray;
  font-size: 1.2rem;
  width: 100%;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius:6px;
  display: inherit;
  flex-wrap: inherit;
  flex-direction: column;
  justify-content: center;
  margin: 10px auto;
  padding: 10px;
  overflow: hidden;
  max-width: 350px;
  width: 50%;
}

.card-text {
  padding: 5px;
  text-align: center;
}

.card-img {
  display: block;
  
  max-height: 170px;
}